<div class="notes-item"><div class="notes-item-top" :class="{between: !grid}"><h3>{{ note.title }}</h3><p>{{ note.date }}</p></div><p>{{ note.text }}</p><div class="notes-item-btns"><button @click="$emit('changeNote', note.id)"><img src="../assets/img/edit.svg" alt="alt"><span>РЕДАКТИРОВАТЬ</span></button><button @click="$emit('delNote', note.id)"><img src="../assets/img/trash.svg" alt="alt"><span>УДАЛИТЬ</span></button></div></div>