<section class="notes"><div class="container"><div class="notes-top"><h2 v-if="search == ''">{{ notes.length <= 0 ? words.noinfobar[lang] : words.infobar[lang] }}</h2><h2 v-else>{{ words.appbarseacrch[lang] }}</h2><button @click="grid = !grid"><img src="../assets/img/list.svg" alt="" v-if="grid"><img src="../assets/img/grid.svg" alt="" v-else><span>{{grid ? 'Список' : 'Сетка'}}</span></button></div><div class="notes-list" :class="{active: !grid}"><NoteItem :grid="grid" v-for="note in notes" :key="note.id" :note="note" @delNote="$emit('delNote', note.id)" @changeNote="$emit('changeNote', note.id)"></NoteItem></div></div></section>