<Transition name="modal"><div class="modal" @click="$emit('showOrClose'), $emit('editFalse')"><div class="modal-block" @click.stop> <h2>{{ edit ? 'Изменить' : 'Добавить' }} заметку</h2><form class="modal-inputs" action="" @submit.prevent="send"><input type="text" placeholder="Title" v-model="user.title" required><textarea placeholder="Content" v-model="user.text" required></textarea><div class="modal-btns"><button type="button" @click="$emit('showOrClose'), $emit('editFalse')">Отмена</button><button v-if="!edit">Добавить</button><button type="button" v-else @click="changeNote">Изменить</button></div></form></div></div></Transition>